@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@700&display=swap);
.facebook-login-button-component{background-color:var(--facebook-blue) !important}

.header-component{position:fixed}.header-component .navbar{padding:0 !important;height:0;z-index:1;width:96vw}.header-component .navbar button{margin-top:1em}.header-component .navbar .nav-item{display:flex}.header-component .navbar .nav-item .button-component{background-color:var(--green)}

.input-component label{font-weight:500}.input-component .error{color:var(--red-dark);font-weight:bold}.input-component.list-group-selector .list-group-item{cursor:pointer}.input-component.list-group-selector .item-label{font-weight:600}.input-component.list-group-selector .item-description{margin-top:0.8em}

.leaderboard-modal-component .modal-content{min-height:90vh}


.link-component,.link-component:hover{color:white}

.loading-component{display:flex;align-items:center;flex-direction:column;width:100%;-webkit-animation:fadein 2s;animation:fadein 2s}@keyframes fadein{from{opacity:0}to{opacity:1}}@-webkit-keyframes fadein{from{opacity:0}to{opacity:1}}

.spinner-component{display:flex;align-items:center;flex-direction:column}.spinner-component .icon-spin{-webkit-animation:icon-spin 2s infinite linear;animation:icon-spin 2s infinite linear}@-webkit-keyframes icon-spin{0%{transform:rotate(0deg)}100%{transform:rotate(359deg)}}@keyframes icon-spin{0%{transform:rotate(0deg)}100%{transform:rotate(359deg)}}

.municipality-progress-component{display:flex;flex-direction:column;justify-content:center;align-items:center;margin-top:0.3em}.municipality-progress-component .name{color:white;text-shadow:0 0 0.2em black;margin-bottom:-0.8em;font-weight:500}.municipality-progress-component .progress{margin-top:1em;width:20em}.municipality-progress-component .progress .offset{background-color:var(--progress-bar-fill)}.municipality-progress-component .progress .remaining{background-color:var(--progress-bar-trail)}

.header_style{margin:1em}.home-page .logo{position:absolute;width:100px;height:100px;top:0em;left:0em;-webkit-filter:drop-shadow(2px 4px 10px white);filter:drop-shadow(2px 4px 10px white)}.home-page .intro_section{background-image:url("/assets/jon-flobrant-oRUQsTX9Zv0-unsplash.jpg");width:100vw;height:100vh;margin:0;background-size:cover;background-attachment:fixed;display:flex;justify-content:center;flex-flow:column nowrap}.home-page .intro_section .counter_and_logo_outer_container{width:100%;padding-top:2em;position:relative;justify-content:center;display:flex;flex-flow:column-nowrap}.home-page .intro_section .counter_and_logo_outer_container .counter_and_logo_container{display:flex;flex-flow:column nowrap;margin:auto;margin-top:10vh;position:absolute;width:20%}.home-page .intro_section .counter_and_logo_outer_container .counter_and_logo_container .municipalities-progress{display:flex;justify-content:center;align-items:center;flex-direction:column;-webkit-animation-name:fadeIn;animation-name:fadeIn;opacity:0;-webkit-animation-iteration-count:1;animation-iteration-count:1;-webkit-animation-duration:1.5s;animation-duration:1.5s;-webkit-animation-delay:2s;animation-delay:2s;-webkit-animation-timing-function:ease-in;animation-timing-function:ease-in;-webkit-animation-fill-mode:forwards;animation-fill-mode:forwards}.home-page .intro_section .counter_and_logo_outer_container .counter_and_logo_container .co2counter{margin:0;margin-bottom:2vw;text-align:center;width:100%;font-family:"Nunito", sans-serif;text-shadow:0 0 0.2em #000;color:white;font-weight:bold;-webkit-animation-name:fadeIn;animation-name:fadeIn;opacity:0;-webkit-animation-iteration-count:1;animation-iteration-count:1;-webkit-animation-duration:1.5s;animation-duration:1.5s;-webkit-animation-delay:1s;animation-delay:1s;-webkit-animation-timing-function:ease-in;animation-timing-function:ease-in;-webkit-animation-fill-mode:forwards;animation-fill-mode:forwards}.home-page .intro_section .counter_and_logo_outer_container .counter_and_logo_container .co2counter p:first-child{font-size:1.5em}.home-page .intro_section .counter_and_logo_outer_container .counter_and_logo_container .co2counter p{margin:0;font-size:1.25em}.home-page .intro_section .counter_and_logo_outer_container .progressbar-container{width:70%;height:35%;max-width:600px}.home-page .intro_section button.offset-yourself{background-color:var(--green);font-weight:500;width:60%;align-self:center}.home-page .intro_section .municipalities-progress{display:flex;justify-content:center;align-items:center;flex-direction:column;margin-top:3em}.home-page .intro_section .municipalities-progress button.view-all-municipalities{margin-top:0.5em;background-color:var(--green)}@media (min-width: 601px){.home-page .chart_section{width:70vw}}.home-page .chart_section{margin:auto;height:100vw;padding:5vw}.home-page .chart_section .chart_container .header_style{text-align:center;margin-bottom:1em}.home-page .info_section{padding-top:4em;margin:auto;width:60vw;max-width:800px;display:flex;flex-flow:column nowrap}.home-page .info_section h2{text-align:center}.home-page .info_section p{line-height:1.5}.home-page .info_section img{width:70px;margin:auto}.home-page .icons_whodunnit{display:none}@-webkit-keyframes fadeIn{from{opacity:0}to{opacity:1}}@keyframes fadeIn{from{opacity:0}to{opacity:1}}

:root{--purple: #6F00FF;--purple-light: #a966ff;--purple-soft: #e2ccff;--progress-bar-trail: #a6a8b6;--progress-bar-fill: #fff234;--chart-tooltip-color: #000;--chart-tooltip-background: #eee;--white: #fff;--green: #3A8617;--facebook-blue: #4267B2;--blue: #1493FF;--pink: #ff1493}html{overflow-x:hidden}.route-container{margin-top:2em}

